.horizontal-scroll {
    display: flex;
    overflow-x: auto;
    gap: 12px;
    padding: 12px 0;
    scroll-snap-type: x mandatory;
    scroll-behavior: smooth;
    justify-content: flex-start;
}

.product-card {
    border: 1px solid #ccc;
    border-radius: 8px;
    background: white;
    text-align: center;
    flex-shrink: 0;
    width: 180px;
    height: auto;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease-in-out;
    margin: 0 auto;
}

.product-card img {
    width: 100%;
    height: 140px;
    object-fit: contain;
    border-radius: 8px 8px 0 0;
}

.product-info {
    padding: 8px 12px;
}

.product-info h2 {
    font-size: 14px;
    font-weight: 700;
    margin: 8px 0;
    color: #333;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
}

.product-price {
    font-size: 12px;
    font-weight: 700;
    color: #007bff;
    margin-bottom: 8px;
}

.product-rating .star {
    color: #ddd;
    font-size: 14px;
}

.product-rating .star.filled {
    color: #f90;
}

.more-button-container {
    justify-content: center;
    justify-self: center;
}

.load-more {
    margin: 16px auto;
    padding: 10px 20px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}

.load-more:hover {
    background-color: #0056b3;
}

.product-detail {
    padding: 20px;
    text-align: center;
}

.product-detail img {
    width: 300px;
    height: 300px;
    -o-object-fit: contain;
    object-fit: contain;
}

.product-description {
    margin-top: 16px;
    font-size: 14px;
    color: #666;
}

.container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
}

.button {
    padding: 12px 24px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}

.button:hover {
    background-color: #0056b3;
}

.horizontal-scroll .product-card {
    flex: 0 0 25%;
    margin-right: 12px;
}

.product-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 16px;
    padding: 16px;
}

.product-grid .product-card {
    width: 90%;
    margin: 0 auto;
}

.product-image {
    width: 100%;
    height: 180px;
    object-fit: contain;
    background-color: #f0f0f0;
    border-radius: 8px;
    padding-top: 10px;
}

.load-more {
    margin: 16px auto;
    padding: 12px 24px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}

.load-more:hover {
    background-color: #0056b3;
}

.popup-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.6);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
}

.popup-content {
    background-color: white;
    padding: 20px;
    border-radius: 8px;
    width: 80%;
    max-width: 600px;
    position: relative;
    overflow-y: auto;
    max-height: 90vh;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.close-popup {
    position: absolute;
    top: 10px;
    right: 10px;
    background: none;
    border: none;
    font-size: 20px;
    color: #333;
    cursor: pointer;
}

.product-detail {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.popup-product-image {
    width: 200px;
    height: 200px;
    object-fit: contain;
    margin-bottom: 20px;
}

.product-name {
    display: flex;
    align-items: center;
    font-size: 20px;
    font-weight: bold;
}

.free-shipping-banner {
    margin-left: 10px;
    display: flex;
    align-items: center;
    color: green;
}

.free-shipping-banner svg {
    margin-right: 5px;
}

.rating-stars {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 4px;
}

.product-detail table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 20px;
    font-size: 14px;
}

.product-detail td {
    padding: 8px;
    border: 1px solid #ddd;
}

.product-detail td:first-child {
    font-weight: bold;
}

.product-detail th {
    padding: 8px;
    background-color: #f4f4f4;
    border: 1px solid #ddd;
    font-weight: bold;
}

.product-card:hover,
.product-grid .product-card:hover {
    transform: none;
}
